/**
 * @title  Radio
 * @description `inverse` 
 */

import React, { Component } from 'react'
import Radio from '../../src'



class Demo1 extends Component{
  constructor(props) {
  	super(props);
  	this.state = {
    	selectedValue: '1'
    };
  }
  handleChange(value) {
    this.setState({selectedValue: value});
  }
  render() {
    return (
      <Radio.RadioGroup
        name="fruits"
        selectedValue={this.state.selectedValue}
        onChange={this.handleChange.bind(this)}>
          <Radio value="1" inverse></Radio>
          <Radio value="2" inverse></Radio>
          <Radio value="3" inverse></Radio>
      </Radio.RadioGroup>   
    )
  }
};

export default Demo1;


