/**
 * @title Radio
 * @description styleradio
 */

import React, { Component } from 'react'
import Radio from '../../src'



class Demo3 extends Component{
  constructor(props) {
  	super(props);
  	this.state = {
    	selectedValue: '1'
    };
  }
  handleChange(value) {
    this.setState({selectedValue: value});
  }
  render() {
    const radioStyle = {
      display: 'block'
    };
    return (
      <div className="demo3">
        <Radio.RadioGroup
          name="team"
          selectedValue={this.state.selectedValue}
          onChange={this.handleChange.bind(this)}>
            <Radio style={radioStyle} value="1" ></Radio>
            <Radio style={radioStyle} value="2" ></Radio>
            <Radio style={radioStyle} value="3" ></Radio>
            <Radio style={radioStyle} value="4" ></Radio>
            <Radio style={radioStyle} value="5" ></Radio>
            <Radio style={radioStyle} value="6" ></Radio>
        </Radio.RadioGroup>
      </div>
    )
  }
};

export default Demo3;


