/**
 * @title radio
 * @description `colors`
 */

import React, { Component } from 'react'
import Radio from '../../src'



class Demo2 extends Component{
  constructor(props) {
  	super(props);
  	this.state = {
    	selectedValue: '3'
    };
  }
  handleChange(value) {
    this.setState({selectedValue: value});
  }
  render() {
    return (
      <Radio.RadioGroup
        name="color"
        selectedValue={this.state.selectedValue}
        onChange={this.handleChange.bind(this)}>
          <Radio colors="primary" value="1" ></Radio>
          <Radio colors="success" value="2" ></Radio>
          <Radio colors="info" value="3" ></Radio>
          <Radio colors="warning" value="4" ></Radio>
          <Radio colors="danger" value="5" ></Radio>
          <Radio colors="dark" value="6" ></Radio>
      </Radio.RadioGroup>
    )
  }
};

export default Demo2;


